.loaderbody {
  display:flex;
  align-items:center;
  min-height: 100vh;
  background-color:#382853;

}
.container_loader {
  display: block;
  height: 300px;
  width: 300px;
  margin: 0 auto;
}
.baton {
  display: block;
  height: 2px;
  width: 70px;
  background-color: #459fa5;
  -webkit-animation: scale 1.25s infinite linear;
          animation: scale 1.25s infinite linear;
  -webkit-transform-origin: 0;
  transform-origin: 0;
  -webkit-transform: rotate(-10deg);
  transform: rotate(-10deg);
}
.baton:before {
  display: block;
  height: 5px;
  width: 5px;
  background-color: #f5a51c;
  position: absolute;
  top: -2px;
  border-radius: 5px;
}
.baton:after {
  display: block;
  height: 5px;
  width: 5px;
  background-color: #f5a51c;
  position: absolute;
  top: -2px;
  right: 0;
  border-radius: 5px;
}
.metronome {
  -webkit-transform-origin: 0;
  transform-origin: 0;
  -webkit-animation: metronome 1.25s infinite linear;
          animation: metronome 1.25s infinite linear;
}
.baton-0 {
  height: 1px;
  width: 150px;
  position: absolute;
  top: calc(50% - 8px);
  -webkit-transform-origin: 100%;
  transform-origin: 100%;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  -o-transform: rotate(0deg);
}
.baton-0 .baton, .baton-0 .baton:after, .baton-0 .metronome {
  -webkit-animation-delay: 0s;
